import React from "react";
import { compose } from "@vxmind/vxhelpers";
import { hot } from "react-hot-loader";
import { Grid, Icon, NavBar } from "antd-mobile";
import { withRouter } from "react-router";
import { PartyPartyHomePageRoutePath } from "../lazyPages";

export function PartyPaidInfoPagePure(
    {
        history
    }: any
) {
    const paidHistory = [
        { month: "一月", isPaid: true },
        { month: "二月", isPaid: true },
        { month: "三月", isPaid: true },
        { month: "四月", isPaid: true },
        { month: "五月", isPaid: true },
        { month: "六月", isPaid: true },
        { month: "七月", isPaid: true },
        { month: "八月", isPaid: false },
        { month: "九月", isPaid: false },
        { month: "十月", isPaid: false },
        { month: "十一月", isPaid: false },
        { month: "十二月", isPaid: false }
    ];

    const renderGridItem = (gridItem) => {
        return (
            <div>
                <strong>
                    {gridItem.month}
                </strong>
                <br/>
                <span style={{ fontSize: 10 }}>
                {gridItem.isPaid
                    ? <span style={{ color: "green" }}>已交</span>
                    : <span style={{ color: "red" }}>未交</span>}
                </span>
            </div>
        );
    };

    return (
        <div>

            <NavBar
                mode="light"
                icon={<Icon type="left"/>}
                onLeftClick={() => history.replace(PartyPartyHomePageRoutePath)}
                style={{ position: "fixed", top: 0, width: "100%" }}
            >
                缴纳记录
            </NavBar>


            <img
                src={require("../images/党费缴交.jpg")}
                style={{ marginTop: 45, width: "100%" }}
            />

            <Grid
                data={paidHistory}
                columnNum={3}
                activeStyle={false}
                renderItem={renderGridItem}
            />

        </div>
    );
}

export const PartyPaidInfoPage = compose(
    hot(module),
    withRouter
)(PartyPaidInfoPagePure);

export default PartyPaidInfoPage;
